.movie-row {
    margin-bottom: 20px;
  }
  
  .movie-row h2 {
    margin: 10px 0;
  }
  
  .movie-container {
    display: flex;
    overflow-x: auto;
    scroll-snap-type: x mandatory;
  }
  
  .movie-card {
    flex: 0 0 auto;
    width: 200px;
    margin-right: 10px;
    scroll-snap-align: start;
  }
  
  .movie-card img {
    width: 100%;
    border-radius: 8px;
  }
  
  .movie-card p {
    text-align: center;
    margin-top: 5px;
  }
  